.post {
    background-color: black;
    color: white;
    padding: 20px; 
    /* position: absolute; */
    top: 0;
    bottom: 0;
    left: 75px; 
    right: 0;
    display: flex;
    flex-direction: column;
    margin-left: 100px;
  }

  hr{
    border: 1px solid #AEAEAE;
    /* margin: 20px 0px; */
    margin-left: 1px;
  }
  .posts{
    background-color: black;
  }
  
  .user-profile {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  } 
   .user-details {
    display: flex;
    align-items: center;
  }
  
  .user-photo {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .user-name {
    color: #cbc9c9;
    font-size: 18px;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden; 
    text-overflow: ellipsis;
  }
  
.project-image {
    border: 1px solid gray;
    border-radius: 12px;
    width: 750px;
    height: 400px;
    /* width: auto; Updated */
   
    object-fit: contain;
    margin-bottom: 20px;
  } 

  
  .project-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .view-project-button {
    background-color: #898989;
    color: #000000;
    font-weight: bold;
    width: 123px;
    border: none;
    margin-right: 8px;
    padding: 10px 10px;
    cursor: pointer;
    border-radius: 5px;
    white-space: nowrap; /* Add this line to prevent line breaks */
  }
  
  .view-project-button:hover{
    background-color: #d0caca;
    font-weight: bolder;
  }
  .project-stats {
    display: flex;
    align-items: center;
    color: gray;
  }
  
  .view-count {
    white-space: nowrap;
    margin-right: 470px;
  }
  
  .project-stats i {
    margin-left: 10px;
    font-size: 24px;
  } 

  .collaborators {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .collaborator-link {
    text-decoration: none;
    color: #898989;
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  
  .comment-section {
    width: 100%;
    background-color: #f5f5f5;
    padding: 10px;
    margin-top: 10px;
  }
  
  .comment {
    margin-bottom: 10px;
    padding: 5px;
    background-color: #ffffff;
    border: 1px solid #dddddd;
  }
  
  
  .collaborator-link i {
    margin-right: 5px;
  }

  
  .search-bar {
    /* margin-right: 0px; */
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding-left: 120px;
   
  }
  
  .search-bar input {
    flex-grow: 1;
    background-color: #121214;
    color: #AEAEAE;
    padding: 15px;

    border: none;
    border: 1px solid #AEAEAE;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    
  }
  
  .search-button {
    border-color: #898989;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #AEAEAE;
    /* padding: 10px; */
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: bolder;
    cursor: pointer;
    /* margin-left: 1px; */
  }
  

.project-description  {
      max-width: 100%;  
      margin-bottom: 20px;
    color: gray;
    font-size: 16px;
    line-height: 1.5;
    margin: 0;
  }
  
i{
    cursor: pointer;
}
