.comment-section {
  background-color: #202123;
  color: black;
  margin-top: 10px;
}

.comment {
  color: white;
  margin-top: 5px;
  background-color: #202123;
  border-radius: 4px;
}

.add-comment {
  display: flex;
  margin-top: 10px;
}

/* commment placehodelr */
.comment-input::placeholder {
  color: #000000;
}
#viewComments{
  font-weight: bold;
  color: white;
}

.comment-input {
  
  flex-grow: 1;
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #000000;
}

.post-comment-button {
  margin-left: 5px;
  padding: 5px 10px;
  border-radius: 4px;
  background-color: gray;
  color: #fff;
  border: none;
  cursor: pointer;
}

.post-comment-button:hover {
  background-color: #444343;
  color: white;
}

.fa-thumbs-up.liked {
  color: #1BA8F0;
}