.file-section {
    background-color: black;
    color: white;
    padding: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 90px; /*match sidebar width */
    right: 0;
    display: flex;
    flex-direction: column;
  }
  
  .search-bar {
    margin-right:100px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .search-bar input {
    
    
    flex-grow: 1;
    background-color: #121214;
    color: #AEAEAE;
    padding: 15px;
    border: none;
    border: 1px solid #AEAEAE;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  
  .search-button {
    border-color: #898989;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #AEAEAE;
    /* padding: 10px; */
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: bolder;
    cursor: pointer;
    /* margin-left: 1px; */
  }
  
  .uploaded-files {
    margin-bottom: 1px;
  }
  h2{
    color: #AEAEAE;
  }
  /* .file-item {
    display: flex;
    flex-direction: row;
    background-color: #2D2D2D;
    padding: 10px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    width: 100%;
  } */
  .file-item {
    background-color: #2D2D2D;
    padding: 10px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    width: calc(100% - 25px);
    padding-left: 25px;
  }
  
  
  .file-item i {
    margin-right: 10px;
    cursor: pointer;
  }
  
  .file-name {
    margin-right: 10px;
    flex-grow: 1;
  }
  
  .file-timestamp {
    margin-right: 10px;
    font-size: 12px;
    color: gray;
  }
  .upload-button {
    cursor: pointer;
    position: fixed;
    bottom: 20px;
    right: 20px;
    /* increase the size */
    padding: 25px;
    border-radius: 50%;
    background-color: #AEAEAE;
    color: black;
    font-size: 20px;
    border: none;
    
  }
  