.app-container {
  display: flex;
  background-color: black;
}


.search-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-bar input {
  flex-grow: 1;
  background-color: #121214;
  color: #AEAEAE;
  padding: 15px;
  border: none;
  border: 1px solid #AEAEAE;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-button {
  border-color: #898989;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #AEAEAE;
  /* padding: 10px; */
  padding-right: 50px;
  padding-left: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: bolder;
  cursor: pointer;
  /* margin-left: 1px; */
}



.profile-container {
  background-color: black;
  color: white;
  width: 300px; /* Adjust the width as per your requirement */
  /* makke height full page */
  height: 100vh;
  padding: 20px;
  margin-right:80px ;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  margin-left: -65px;
}

.profile-details {
  display: flex;
  flex-direction: column;
  align-items: left;
}

.profile-image img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  cursor: pointer;
}

.profile-username {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
}

.profile-follow {
  margin-top: 10px;
  background-color: #202123;
  color: white;
  border: none;
  padding: 8px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid white;
  border-radius: 10px;
}
.profile-follow:hover{
  background-color: white;
  color: #202123;
  font-weight: bold;
}
.profile-followers {
  margin-top: 5px;
  font-size: 14px;
}

.profile-introduction {
  margin-top: 15px;
  text-align: left;
}

.profile-links {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: left;
}


.profile-link {
  text-decoration: underline;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: white;
  margin-bottom: 5px;
}

.profile-link i {
  margin-right: 5px;
}

/* Additional component CSS */
.additional-component {
  background-color: grey;
  color: white;
  padding: 20px;
  width: 300px; /* Adjust the width as per your requirement */
  margin-top: 20px;
}

.profile-page{
  display:flex;
  flex:20%;
  }