
.home-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.column_updates{
  flex:0.3;
  z-index: 1;
}


.vl {
  border-left: 2px solid rgb(255, 255, 255);
  /* height: 100vh;
  position: absolute; */
}

.column_posts{
  flex:0.6;
}

.column {
  flex-basis: 48%;
}

.posts {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
}

.updates {
  background-color: green;
  padding: 20px;
  border-radius: 8px;
  color: #fff;
}

.posts h2,
.updates h2 {
  margin-bottom: 10px;
}



.search-bar { 
  margin-top: 20px;
  display: flex;
  align-items: center; 
  margin-bottom: 20px;
  margin-left: 0px;
}

.search-bar input {
  flex-grow: 1;
  background-color: #121214;
  color: #AEAEAE;
  padding: 15px;
  border: none;
  border: 1px solid #AEAEAE;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-button {
  border-color: #898989;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #AEAEAE;
  /* padding: 10px; */
  padding-right: 50px;
  padding-left: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: bolder;
  cursor: pointer;
  /* margin-left: 1px; */
}

