.main-content {
  background-color: black;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .file-container {
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  

.app-container {
    display: flex;
  }
  
  .sidebar {
    position: fixed;
    background-color: #202123;
    color: white;
    width: 90px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .logo {
    background-color: #202123;
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
  }
  
  .logo img {
    width: 50px;
    height: auto;
    background-color: #202123;
  }
  
  .menu {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }
  
  .menu-item {
    background-color: #202123;
    color: grey;
    border: none;
    padding: 10px;
    width: 100%;
    text-align: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .menu-item i {
    margin-bottom: 5px;
  }
  
  .menu-item.active {
    background-color: #202123;
    color: white;
  }
  
  .menu-item:hover {
    background-color: grey;
    color: white;
  }
  
  .profile-container {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  