.messages-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.message-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.message {
  text-align: center;
  background-color: grey;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.message-content {
  flex-grow: 1;
}

.message-time {
  color: #eeeeee;
  font-size: 12px;
  margin-left: 1px;
}



.message-icon {
  
  color: #777;
  font-size: 20px;
  margin-right: 10px;
}