.messages-container {
    background-color: rgb(0, 0, 0);
    color: white;
    padding: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 90px; /*match sidebar width */
    right: 0;
    display: flex;
    flex-direction: column;
  }
  

  
  .search-bar {
    margin-left: 0px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .search-bar input {
    margin-left: 0px;
    flex-grow: 1;
    background-color: #121214;
    color: #AEAEAE;
    padding: 15px;
    border: none;
    border: 1px solid #AEAEAE;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  
  .search-button {
    border-color: #898989;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #AEAEAE;
    /* padding: 10px; */
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: bolder;
    cursor: pointer;
    /* margin-left: 1px; */
  }
  

  .messages-container h1 {
    font-size: 24px;
    font-weight: bold;
  }
  
  .message-list {
    margin-top: 20px;
  }
  
  .message {
    border-radius: 10px;
    cursor: pointer;
    margin-right: 100px;
    margin-bottom: 10px;
    padding: 10px;
    background-color: grey;
  }
  
  .sender {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .name {
    font-size: 16px;
  }
  
  .time {
    font-size: 14px;
    color: black;
  }
  
  .content {
    color: black;
    padding-left: 10px;
    padding-top: 10px;
    margin-top: 10px;
    font-size: 14px;
  }
  