.projects-container {
    background-color: black;
    color: white;
    padding: 20px;
    top: 0;
    bottom: 0;
    left: 75px;
    right: 0;
    margin-left: -70px;
    display: flex;
    flex-direction: column;
}

.tab-menu {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.tab {
    border-radius: 10px;
    margin: 10px;
    background-color: #7b7c7e;
    color: #c1c1c1;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}



.updates-section,
.announcements-section {
    margin-top: 20px;
}

.news-item {
    padding: 2px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 10px;
}

.news-item .news-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 20px;
}

.news-item .news-content {
    flex: 1;
}

.news-item .news-description {
    margin-bottom: -20px;
}

.news-item .news-time {
    color: #777;
    font-size: 14px;
}

.news-item .news-content {
    border-radius: 10px;
}

.tab.active {
    color: "white";
    background-color: "black";
    border-bottom: "2px solid white";
    border-radius: "0px";
    padding: "10px";
}