.notifications-container {
    background-color: black;
    color: white;
    padding: 20px;
    /* height: 100vh; */
    /* position: absolute; */
    top: 0;
    bottom: 0;
    left: 75px; 
    right: 0;
    margin-left: -70px;
    display: flex;
    flex-direction: column;
  }
  
  .tab-menu {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .tab {
    border-radius: 10px;
    margin: 10px;
    background-color: #7B7C7E;
    color: #202020;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .tab.active {
    background-color: #C1C1C1;
    color: #202020;
    cursor: pointer;
  }
  
  .updates-section,
  .announcements-section {
    margin-top: 20px;
  }
  
  .news-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .news-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 20px;
  }
  
  .news-content {
    flex: 1;
  }
  
  .news-description {
    margin-bottom: -20px;
  }
  
  .news-time {
    color: #777;
    font-size: 14px;
  }
  